<template>
  <div class="page-header clear-filter" filter-color="orange">
    <div
      class="page-header-image"
      style="background-image: url('img/login.jpg')"
    ></div>
    <div class="content">
      <div class="container">
        <div class="col-md-5 ml-auto mr-auto">
          <card type="login" plain>
            <div slot="header" class="logo-container">
              <img v-lazy="'img/TTT-logo.png'" alt="" />
            </div>

            <fg-input
              v-model="email"
              class="no-border input-lg"
              addon-left-icon="now-ui-icons ui-1_email-85"
              placeholder=""
              type="email"
            >
            </fg-input>

            <fg-input
              v-model="pw"
              @keypress.enter="login"
              class="no-border input-lg"
              addon-left-icon="now-ui-icons ui-1_lock-circle-open"
              placeholder=""
              type="password"
              autocomplete="off"
            >
            </fg-input>

            <template slot="raw-content">
              <div class="card-footer text-center">
                <n-button
                  @click="login"
                  type="primary"
                  block
                  round
                >
                  
                </n-button>
              </div>
              <div class="pull-left">
                <h6>
                  <n-button type="neutral" @click.native="modals.signup = true" link>
                    TTT's   <i class="now-ui-icons sport_user-run"></i>
                  </n-button>
                  <modal :show.sync="modals.signup" headerClasses="justify-content-center">
                    <signup-form/>
                  </modal>
                </h6>
              </div>
              <div class="pull-right">
                <h6>
                  <n-button type="neutral" @click.native="modals.help = true" link>
                    TTT ?
                  </n-button>
                  <modal :show.sync="modals.help" headerClasses="justify-content-center" style="color:black">
                    <h4 slot="header" class="title title-up">TTT?</h4>
                    <p>
                      1. auTo Trading sysTem . <br/>
                      2.  Trust, Trend, Tactic  3T   . <br/>
                    </p>
                    <!-- <template slot="footer">
                      <n-button>Nice Button</n-button>
                      <n-button type="danger" @click.native="modals.classic = false">Close</n-button>
                    </template> -->
                  </modal>
                </h6>
              </div>
            </template>
          </card>
        </div>
      </div>
    </div>
    <main-footer></main-footer>
  </div>
</template>
<script>
import { Card, Button, FormGroupInput, Modal } from '@/components';
import MainFooter from '@/layout/MainFooter';
import SignupForm from '@/pages/components/SignupForm';
import firebase from 'firebase';

export default {
  name: 'login-page',
  bodyClass: 'login-page',
  components: {
    Card,
    MainFooter,
    [Button.name]: Button,
    [FormGroupInput.name]: FormGroupInput,
    Modal,
    SignupForm
  },
  data(){
    return {
      email:'',
      pw:'',
      modals: {
        signup: false,
        help: false
      }
    }
  },
  methods: {
    login() {
      firebase.auth()
        .signInWithEmailAndPassword(this.email, this.pw)
        .then(
          user => {
            this.$session.set('user', user);
            this.$router.replace("/");
            location.reload();
          },
          function(err) {
            alert(": " + err.message);
          }
        );
    },
  },
};
</script>
<style></style>
