<template>
    <div>
        <div class="row">
            <div class="col-lg-4">
                <card class="chart">
                    <bar-chart
                            class="chart-area"
                            chart-id="blue-bar-chart"
                            :chart-data="blueBarChart.chartData"
                            :gradient-stops="blueBarChart.gradientStops"
                            :extra-options="blueBarChart.extraOptions">
                    </bar-chart>
                </card>
            </div>
            <div class="col-lg-4">
                <card class="chart">
                    <line-chart style="height: 100%"
                                :chart-data="purpleLineChart.chartData"
                                :gradient-color="purpleLineChart.gradientColors"
                                :gradient-stops="purpleLineChart.gradientStops"
                                :extra-options="purpleLineChart.extraOptions">
                    </line-chart>
                </card>
            </div>
        </div>
        <div class="small">
            <card class="chart">
            <h5 class="card-category">completedTasks</h5>
            <h3 class="card-title"><i class="tim-icons icon-send text-success "></i> 12,100K</h3>
            <line-chart
                    class="chart-area"
                    chart-id="purple-line-chart"
                    :chart-data="purpleLineChart2.chartData"
                    :gradient-stops="purpleLineChart2.gradientStops"
                    :extra-options="purpleLineChart2.extraOptions">
            </line-chart>
            </card>
        </div>
        <div class="row">
            <div class="col-12">
                <card>
                <template slot="header">
                <div class="row">
                    <div class="col-sm-6 text-left">
                        <h5 class="card-category">Total Shipments</h5>
                        <h2 class="card-title">Performance</h2>
                    </div>
                    <div class="col-sm-6">
                    <div class="btn-group btn-group-toggle float-right"
                        data-toggle="buttons">
                        <template>
                            <label v-for="(option, index) in bigLineChartCategories"
                                    :key="option"
                                    class="btn btn-success btn-sm btn-simple"
                                    :class="{active:bigLineChart.activeIndex === index}"
                                    :id="index">
                                <input type="radio"
                                    @click="initBigChart(index)"
                                    name="options" autocomplete="off"
                                    :checked="bigLineChart.activeIndex === index">
                                {{ option }}
                            </label>
                        </template>
                    </div>
                    </div>
                </div>
                </template>
                    <line-chart
                                class="chart-area"
                                ref="bigChart"
                                chart-id="big-line-chart"
                                :chart-data="bigLineChart.chartData"
                                :gradient-colors="bigLineChart.gradientColors"
                                :gradient-stops="bigLineChart.gradientStops"
                                :extra-options="bigLineChart.extraOptions">
                    </line-chart>
                </card>
            </div>
        </div>
    </div>
</template>

<script>
import {
  Card
} from "@/components/index";

import LineChart from '@/components/Charts/LineChart.js'
import BarChart from '@/components/Charts/BarChart.js'
import * as chartConfigs from '@/components/Charts/config';
import config from '@/config';

export default {
    components: {
        Card,
        LineChart,
        BarChart,
    },
    data () {
        return {
            blueBarChart: {
                extraOptions: chartConfigs.barChartOptions,
                chartData: {
                labels: ['USA', 'GER', 'AUS', 'UK', 'RO', 'BR'],
                datasets: [{
                    label: "Countries",
                    fill: true,
                    borderColor: config.colors.info,
                    borderWidth: 2,
                    borderDash: [],
                    borderDashOffset: 0.0,
                    data: [53, 20, 10, 80, 100, 45],
                }]
                },
                gradientColors: config.colors.primaryGradient,
                gradientStops: [1, 0.4, 0],
            },
            purpleLineChart: {
                extraOptions: chartConfigs.purpleChartOptions,
                chartData: {
                labels: ['JUL', 'AUG', 'SEP', 'OCT', 'NOV', 'DEC'],
                datasets: [{
                    label: "Data",
                    fill: true,
                    borderColor: '#d048b6',
                    borderWidth: 2,
                    borderDash: [],
                    borderDashOffset: 0.0,
                    pointBackgroundColor: '#d048b6',
                    pointBorderColor: 'rgba(255,255,255,0)',
                    pointHoverBackgroundColor: '#d048b6',
                    pointBorderWidth: 20,
                    pointHoverRadius: 4,
                    pointHoverBorderWidth: 15,
                    pointRadius: 4,
                    data: [80, 100, 70, 80, 120, 80],
                }]
                }
            },
            purpleLineChart2: {
                extraOptions: chartConfigs.purpleChartOptions,
                chartData: {
                labels: ['JUL', 'AUG', 'SEP', 'OCT', 'NOV'],
                datasets: [{
                    label: "My First dataset",
                    fill: true,
                    borderColor: config.colors.danger,
                    borderWidth: 2,
                    borderDash: [],
                    borderDashOffset: 0.0,
                    pointBackgroundColor: config.colors.danger,
                    pointBorderColor: 'rgba(255,255,255,0)',
                    pointHoverBackgroundColor: config.colors.danger,
                    pointBorderWidth: 20,
                    pointHoverRadius: 4,
                    pointHoverBorderWidth: 15,
                    pointRadius: 4,
                    data: [90, 27, 60, 12, 80],
                }]
                },
                gradientColors: ['rgba(66,134,121,0.15)', 'rgba(66,134,121,0.0)', 'rgba(66,134,121,0)'],
                gradientStops: [1, 0.4, 0],
            },
            bigLineChartCategories:[
                "Accounts",
                "Purchases",
                "Sessions"
            ],
            bigLineChart: {
                allData: [
                [100, 70, 90, 70, 85, 60, 75, 60, 90, 80, 110, 100],
                [80, 120, 105, 110, 95, 105, 90, 100, 80, 95, 70, 120],
                [60, 80, 65, 130, 80, 105, 90, 130, 70, 115, 60, 130]
                ],
                activeIndex: 0,
                chartData: { datasets: [{ }]},
                extraOptions: chartConfigs.purpleChartOptions,
                gradientColors: config.colors.primaryGradient,
                gradientStops: [1, 0.4, 0],
                categories: []
            },
        }
    },
  methods:{
    initBigChart(index) {
      let chartData = {
        datasets: [{
          fill: true,
          borderColor: config.colors.primary,
          borderWidth: 2,
          borderDash: [],
          borderDashOffset: 0.0,
          pointBackgroundColor: config.colors.primary,
          pointBorderColor: 'rgba(255,255,255,0)',
          pointHoverBackgroundColor: config.colors.primary,
          pointBorderWidth: 20,
          pointHoverRadius: 4,
          pointHoverBorderWidth: 15,
          pointRadius: 4,
          data: this.bigLineChart.allData[index]
        }],
        labels: ['JAN', 'FEB', 'MAR', 'APR', 'MAY', 'JUN', 'JUL', 'AUG', 'SEP', 'OCT', 'NOV', 'DEC'],
      }
      this.$refs.bigChart.updateGradients(chartData);
      this.bigLineChart.chartData = chartData;
      this.bigLineChart.activeIndex = index;
    }
  },
  mounted(){
    this.initBigChart(0);
  },
}
</script>

<style>
  .small {
    max-width: 600px;
    margin:  150px auto;
  }
</style>